body {
    margin: 0;
}

a {
    text-decoration: none;
}

a:link, a:visited, a:hover, a:active {
    color: black;
}

.header-warning {
    background-color: yellow;
    text-align: center;
    font-size: 1.5em;
    font-weight: bold;
    text-transform: uppercase;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
}

/* 
VARIABLES
*/

:root {
    --default-purple: #5e30a8;
}